<script lang="ts" setup>
import { reactive } from 'vue'
import VChart from 'vue-echarts'
import * as echarts from 'echarts/core'
import { LineChart } from 'echarts/charts'
import { TitleComponent, TooltipComponent, GridComponent, LegendComponent } from 'echarts/components'
import { CanvasRenderer } from 'echarts/renderers'

// 注册 ECharts 组件
echarts.use([
  LineChart,
  TitleComponent,
  TooltipComponent,
  GridComponent,
  LegendComponent,
  CanvasRenderer
])

// 模拟统计数据
const stats = reactive({
  today_total_orders: 120,
  today_paid_orders: 95,
  today_paid_amount: 3450
})

// 最近7天订单变化（模拟）
const last7DaysOrders = reactive({
  dates: ['2025-09-10','2025-09-11','2025-09-12','2025-09-13','2025-09-14','2025-09-15','2025-09-16'],
  total: [100, 120, 90, 130, 110, 125, 140],
  paid: [80, 95, 70, 110, 85, 100, 120]
})

// ECharts 配置
const chartOption = reactive({
  tooltip: { trigger: 'axis' },
  legend: { data: ['总订单','支付订单'], top: 10 },
  grid: { left: '3%', right: '4%', bottom: '3%', containLabel: true },
  xAxis: { type: 'category', data: last7DaysOrders.dates },
  yAxis: { type: 'value' },
  series: [
    { name: '总订单', type: 'line', data: last7DaysOrders.total, smooth: true },
    { name: '支付订单', type: 'line', data: last7DaysOrders.paid, smooth: true }
  ]
})

// 渠道订单排行榜（模拟数据）
const channelRanking = reactive([
  { rank: 1, channel: '渠道A', orders: 50 },
  { rank: 2, channel: '渠道B', orders: 45 },
  { rank: 3, channel: '渠道C', orders: 40 },
  { rank: 4, channel: '渠道D', orders: 35 },
  { rank: 5, channel: '渠道E', orders: 30 },
])
</script>

